      border-radius: 8px;
      font-size: 14px;
      font-weight: 500;
      cursor: pointer;
      transition: all 0.2s ease;

      ion-icon {
        font-size: 18px;
        color: white;
        margin: 0;
      }

      &:hover {
        background: #4338CA;
        transform: translateY(-1px);
      }

      &:active {
        transform: translateY(0);
      }
    }
  }
}